@import "~antd/dist/antd.css";

#login_main {
  width: 100%;
  height: 100%;
  background: url("../image/bg.png");
}
.LoginHeader {
  background-color: white;
  height: 60px;
  width: 350px;
  position: absolute;
  top: 200px;
  right: 100px;
}
.LoginHeader_login {
  font-size: 20px;
  height: 100%;
  width: 50%;
  float: left;
  line-height: 60px;
  font-weight: 400;
  color: rgba(75, 75, 75, 1);
 
}
.LoginHeader_login_click {
  font-size: 20px;
  height: 100%;
  width: 50%;
  float: left;
  line-height: 60px;
  font-weight: 400;
  color: #336fdf;
}
.LoginHeader_register {
  font-size: 20px;
  height: 100%;
  width: 50%;
  float: right;
  line-height: 60px;
  font-weight: 400;
  color: rgba(75, 75, 75, 1);

}
.LoginHeader_register_click {
  font-size: 20px;
  height: 100%;
  width: 50%;
  float: right;
  line-height: 60px;
  font-weight: 400;
  color: #336fdf;
}
#LoginMore{
  height:300px;
  width:350px;
  position: absolute;
  top: 260px;
  right: 100px;
  background-color: white;
}
#RegisterMore{
  height:500px;
  width:350px;
  position: absolute;
  top: 260px;
  right: 100px;
  background-color: white;
}
#RegisterMore  .ant-form-item{
  margin-bottom:10px
}

