@import "~antd/dist/antd.css";
.ListTemplate .ListTemplateTitle {
    width: 100%;
    height: 30px;
    margin-top: 20px;
}

.ListTemplate .ListTemplateTitle .ListTemplateTitleIcon {
    width: 7px;
    height: 22px;
    background: rgba(0, 146, 255, 1);
    float: left;
}

.ListTemplate .ListTemplateTitle .ListTemplateTitleFont {
    font-size: 16px;
    /* font-family: SourceHanSansCN-Normal, SourceHanSansCN; */
    font-weight: 400;
    float: left;
    margin-left: 5px;
}

.ListTemplate .mainTable {
    width: 100%;
    height: 680px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 0px 13px 0px rgba(219, 233, 254, 1);
    border-radius: 2px;
    margin-top: 10px;
}

.ListTemplate .bottom {
    width: 100%;
    height: 50px;
    text-align: center;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: rgba(160, 160, 160, 1);
    margin-top: 50px
}

.ListTemplate .mainInput {
    width: 100%;
    height: 50px;
}

.ListTemplate .tableInfo {
    width: 100%;
    height: 650px;
    margin-top: 5px;
}


/* .ListTemplate .ant-table {
  height: 352px !important;
} */

::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}


/*滑动轨道*/

::-webkit-scrollbar-track {
    border-radius: 0px;
    background: none;
}


/*滑块*/

::-webkit-scrollbar-thumb {
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .2);
    background-color: rgba(255, 255, 255, .75)
}


/*滑块效果*/

::-webkit-scrollbar-thumb:hover {
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .2);
    background-color: rgba(85, 85, 85, 0.4);
}